<template>
  <v-app>
    <v-main :class="'page-' + currentPage">
        <div
          class="heading"
          :class="[{ 'heading-home': isHome }]"
          v-if="currentPage !== 'welcome'"
        >
          <img src="./assets/KV-Title.png" alt="logo" class="logo" />
        </div>
        <router-view></router-view>
    </v-main>
  </v-app>
</template>

<script>
export default {
  name: 'App',
  data() {
    return {
      isHome: false,
    };
  },
  computed: {
    currentPage() {
      return this.$router.currentRoute.name.toString().toLowerCase();
    },
  },
  created() {
    if (this.$router.currentRoute.name === 'Home') {
      this.isHome = true;
    }
    console.log(this.currentPage);
  },
};
</script>
