<template>
  <div>
    <v-container class="welcome-master-container">
      <div class="welcome">
        <img src="../assets/KV-Title.png" alt="logo" class="logo" />
        <div class="welcome-container" v-if="name !== ''">
          <h2 class="mb-0 font-weight-light">Chào mừng Quý khách</h2>
          <h2 class="mb-0 customer-name">{{ name }}</h2>
          <h2 class="mb-0 font-weight-light">Đã đến tham gia chương trình.</h2>
        </div>
      </div>
      <img src="../assets/KV-Footer.jpg" alt="footer" class="footer" />
    </v-container>
  </div>
</template>
<script>
export default {
  name: 'WelcomeComponent',
  data() {
    return {
      link: '',
    };
  },
  computed: {
    name() {
      if (this.link) {
        const nameRegexp = new RegExp('\\?welcome=(.*)$', 'gm');
        const match = nameRegexp.exec(this.link);
        if (match[1]) {
          return decodeURI(match[1]);
        }
        return '';
      }
      return '';
    },
  },
  created() {
    const customerRef = this.$database
      .ref('customers')
      .orderByChild('dateAdded')
      .limitToLast(1);
    customerRef.on('value', (snapshot) => {
      localStorage.clear();
      snapshot.forEach((childSnapshot) => {
        // const childKey = childSnapshot.key;
        const childData = childSnapshot.val();
        const { link } = childData;
        this.link = link;
      });
    });
  },
};
</script>
<style lang="scss" scoped>
.logo {
  max-width: 300px;
}

.customer-name{
  font-family: corpoS, 'arial';
}

.footer {
  position: aboslute;
  bottom: 0;
  left: 0;
  width: 100%;
}

.welcome {
  width: 100%;
  height: calc(100% - 156px);
  @media screen and (max-width: 1024px) {
    height: calc(100% - 150px);
  }
  color: white;
  padding: 1rem;
  .welcome-container {
    margin: 0 0.75rem;
  }
}
.welcome-master-container {
  background-image: url('../assets/KV-Car.jpg');
  background-size: contain;
  background-position: center top;
  height: 100vh;
  position: relative;
}
</style>
