<template>
  <div class="checkin">
    <div>
      <h1>Check in</h1>
      <div class="stream-container">
        <qrcode-stream @decode="onDecode"></qrcode-stream>
      </div>
    </div>
  </div>
</template>
<script>
import { QrcodeStream } from 'vue-qrcode-reader';

export default {
  name: 'HomeComponent',
  components: {
    QrcodeStream,
  },
  methods: {
    onDecode(decodedString) {
      // push data to firebase
      const newRecord = this.$database.ref('customers').push();
      newRecord.set({ link: decodedString });

      this.$router.go(this.$router.currentRoute);
    },
  },
};
</script>
