const defaultState = {
  userInformation: null,
};

// actions
const actions = {
  setLang({ commit }, payload) {
    commit('SET_LANG', payload);
  },
  setUserInformation({ commit }, payload) {
    commit('SET_USER_INFORMATION', payload);
  },
};

// mutations
const mutations = {
  SET_LANG(state, payload) {
    this.$i18n.locale = payload;
  },

  SET_USER_INFORMATION(state, payload) {
    state.userInformation = { ...state.userInformation, ...payload };
  },

  SET_USER_INFORMATION_RECORD(state, payload) {
    if (Object.keys(payload).length > 0) {
      Object.keys(payload).forEach((key) => {
        state.userInformation[key] = payload[key];
      });
    }
  },
};

// getters
const getters = {};

export default {
  namespaced: true,
  state: defaultState,
  getters,
  actions,
  mutations,
};
